.flex {
  display: flex;
}

.flex-center {
  display: flex;
  align-items: center;
}

.red-text {
  color: red;
}

.white-background {
  background-color: white;
}

.module-textarea {
  width: 100%;
  padding: 4px;
}

#new-module-name-label {
  margin-right: 5px;
}

#new-module-textarea {
  margin-top: 10px;
  min-height: 3em;
}

p.output-text {
  white-space: pre-wrap;
}

ul.horizontal-select {
  list-style-type: none;
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 5px;
}

ul.horizontal-select > li {
  padding: 5px;
  background-color: #5e5e5e;
  color: white;
  border: 1px solid black;
}

ul.horizontal-select > li.active {
  background-color: lightgreen;
  color: rgb(0, 0, 0);
}

ul.module-select > li {
  margin-right: 5px;
}

ul.module-select.draggable > li {
  cursor: pointer;
}

.remove-button,
.add-button {
  font-size: 0.8em;
  margin: 0 0.2em;
  border-radius: 3px;
  border-width: 1px;
  height: 1.3em;
  width: 1.3em;
}

.remove-button:hover,
.add-button:hover {
  transform: translateY(-0.7px);
}

.remove-button:active,
.add-button:active {
  transform: translateY(0.7px);
}

.remove-button {
  background: linear-gradient(141deg, rgb(227, 143, 136), rgb(234, 49, 36));
}

.add-button {
  background: linear-gradient(141deg, rgb(88, 221, 108), rgb(50, 176, 69));
  height: 2em;
  width: 2em;
}

.save-button,
.edit-button,
.confirm-button {
  height: 1.5em;
  width: 1.5em;
  border-radius: 3px;
  border-width: 1px;
}

.edit-button,
.confirm-button {
  height: 2em;
  width: 2em;
  padding: 3px;
}

.edit-button {
  margin-left: 5px;
}

.save-button:hover {
  background-color: #dcffc6;
}

.edit-button:hover,
.confirm-button:hover {
  transform: translateY(-1px);
}

.edit-button:active,
.confirm-button:active {
  transform: translateY(1px);
}

.save-button img,
.edit-button img,
.confirm-button img {
  max-width: 100%;
  max-height: 100%;
}

.save-button img {
  margin: 1px;
}

.module-exists-button {
  margin-left: 5px;
  height: 1.4rem;
  width: 1.4rem;
}

button#copy-to-clipboard {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.25em;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  min-height: 3em;
  padding: calc(0.875em - 1px) calc(1.5em - 1px);
  margin-bottom: 0.4em;
  transition: all 250ms;
}

button#copy-to-clipboard:hover,
button#copy-to-clipboard:focus {
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
}

button#copy-to-clipboard:hover {
  transform: translateY(-1px);
}

button#copy-to-clipboard:active {
  background-color: #f0f0f1;
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  transform: translateY(0.5px);
}

#feedback {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 10px;
}

#feedback p {
  padding: 0.5em 2em;
  background: rgb(253, 113, 108);
  margin: 0.5em 0em;
  border-radius: 10px;
  font-weight: bold;
}

#feedback.copied p {
  background: greenyellow;
}

#checkmark {
  display: inline-block;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  background-color: #fa6900;
  position: relative;
}

#checkmark:after {
  position: absolute;
  top: 4.5px;
  left: 4px;
  content: "\274c"; /* use the hex value here... */
  font-size: 18px;
  color: transparent;
  text-shadow: 0 0 0 white;
  text-align: center;
}

.copied #checkmark {
  background: #c9ff00;
  -ms-transform: rotate(45deg); /* IE 9 */
  -webkit-transform: rotate(45deg); /* Chrome, Safari, Opera */
  transform: rotate(45deg);
}

.copied #checkmark:before {
  content: "";
  position: absolute;
  width: 4.5px;
  height: 13.5px;
  background-color: #fff;
  left: 16.5px;
  top: 9px;
}

.copied #checkmark:after {
  content: "";
  position: absolute;
  width: 4.5px;
  height: 4.5px;
  background-color: #fff;
  left: 12px;
  top: 18px;
}

/*  Creating the hamburger */
.hamburger {
  height: 1em;
  margin-right: 0.2em;
  cursor: pointer;
}
