* {
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100%;
}

body {
  background: linear-gradient(
    0deg,
    rgba(159, 204, 255, 1) 0%,
    rgba(160, 203, 252, 1) 17%,
    rgba(108, 176, 255, 1) 100%
  );
  padding: 2em;
  font-size: 16px;
  line-height: 1.5em;
}

a {
  color: black;
}

h1,
h2,
h3,
p {
  margin: 0.5em 0;
}

p:last-child {
  margin-bottom: 0;
}

.page-header,
.page-footer,
main {
  padding: 2em;
}

.page-header,
.page-footer {
  background: black;
  color: white;
}

.page-header a {
  color: white;
  text-decoration: none;
}

.main-menu a {
  background-color: #666;
  padding: 0.5em 1em;
  margin-right: 2em;
  font-weight: bold;
}

.main-menu a:hover {
  background: white;
  color: black;
}

.main-menu a.active {
  background: lightgreen;
  color: black;
}

main {
  background: white;
}

.posts > article {
  background: #ddd;
  padding: 1em;
  margin-bottom: 1em;
}

.posts > article:last-child {
  margin-bottom: 0;
}

form {
  margin-bottom: 2em;
}

form label {
  font-weight: bold;
  margin-bottom: 1em;
}

form label:last-child {
  margin-bottom: 0;
}

form input,
form textarea {
  width: 50%;
}

.hidden {
  display: none;
}

.border-box {
  box-sizing: border-box;
}

.relative {
  position: relative;
}

.justify-end {
  justify-content: flex-end;
}

.full-width {
  width: 100%;
}
